.about-container {
    margin-bottom: auto;
    /* height: 80vh; */
    /* min-height: 300px; */
    min-height: 100vh;
    width: 100vw;
    background-color: rgb(236, 232, 232);
    display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: center;
	align-items: center;
    align-content: center;
    overflow-y: auto;
}

.about-header {
    margin-top: 2px;
    height: 30px;
    width: 100vw;
    background-color: #6495ed;
    display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: space-around;
	align-items: baseline;
	align-content: stretch;
    /* z-index: 12; */
}

div.about-header a {
    color: #555;
    width: 40px;
}

